body {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

ul, li {
  margin: 0;
  list-style: none;
}

.header {
  background-image: url("header-bg.079def2d.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 40px 70px 97px;
}

.lib {
  background-image: url("header-bg-lib.cf11223e.png");
  padding: 40px 70px 81px;
}

.header h1 {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 30px;
  line-height: 1.167;
}

.header__title_wrapper {
  align-items: center;
  display: flex;
}

.header__title_wrapper img {
  margin-right: 10px;
}

.header-top {
  justify-content: space-between;
  margin-bottom: 37px;
  display: flex;
}

.header-top ul {
  list-style: none;
  display: flex;
}

.header-top ul li:first-child {
  margin-right: 40px;
}

.header-top ul a {
  text-transform: uppercase;
  color: #fff;
  font-size: 12px;
  line-height: 1.167;
  position: relative;
}

.header-top ul a:after {
  content: "";
  opacity: 0;
  width: 100%;
  height: 100%;
  border-bottom: 3px solid #ff6b08;
  transition: all .5s;
  position: absolute;
  bottom: -20%;
  left: 0;
  box-shadow: inset 0 -1px #ff6b08;
}

.header-top ul a:hover:after {
  opacity: 1;
}

.header__form {
  text-align: center;
  width: 336px;
  border-bottom: .5px solid #fff;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.header__form input {
  color: #fff;
  width: 100%;
  background-color: #0000;
  border: none;
  outline: none;
  padding: 4px;
}

.header input::placeholder {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: .875;
}

.button__search {
  background: none;
  border: none;
}

.lybrary {
  justify-content: center;
  margin: 0 auto;
  display: none;
}

.main {
  min-height: 100vh;
  background-color: #fff;
  padding: 60px 70px;
}

#watched, #queue {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  width: 136px;
  height: 44px;
  border-radius: 5px;
  font-size: 12px;
  line-height: 16px;
  transition: all .25s;
}

#watched {
  background-color: #ff6b01;
  border: none;
}

#queue {
  background-color: #0000;
  border: 1px solid #fff;
  margin-left: 15px;
}

#watched:hover, #watched:focus, #queue:hover, #queue:focus {
  color: #ff6b01;
  opacity: 1;
  background-color: #fff;
  border: none;
}

.movies {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.movie__cart {
  width: 274px;
  height: 440px;
  margin-bottom: 30px;
}

.movie__poster {
  width: 274px;
  height: 398px;
  border-radius: 5px;
  display: block;
}

.movie__title {
  color: #000;
  margin-top: 10px;
  font-size: 12px;
  line-height: 16px;
}

.movie__text-container {
  display: flex;
}

.movie__description {
  color: #ff6b08;
  font-size: 12px;
  line-height: 16px;
}

.movie__rating {
  text-align: center;
  color: #fff;
  background-color: #ff6b01;
  border-radius: 5px;
  margin-left: 11px;
  padding: 1px 10px;
  font-size: 12px;
  line-height: 14px;
  display: block;
}

.footer {
  background-color: #f7f7f7;
  padding: 30px;
}

.footer__container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__container p, .footer__container span, .footer__container a {
  color: #545454;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.188;
}

.footer__link {
  display: flex;
}

.footer__container img {
  margin: 0 10px;
}

.footer__container span {
  margin-right: 5px;
}

.footer__container a {
  text-decoration: underline;
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 50px auto;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (max-width: 558px) {
  .container {
    max-width: 558px;
  }

  .header__title_wrapper h1 {
    display: none;
  }

  .footer__container {
    flex-direction: column;
  }

  .footer__link {
    display: flex;
  }

  .footer__container p {
    margin-bottom: 10px;
  }

  .header__form {
    width: 280px;
  }

  .header {
    padding: 58px 20px 97px;
  }

  .footer__container p, .footer__container span, .footer__container a {
    font-size: 14px;
    line-height: 1.143;
  }
}

